@import "./reset.scss";

img{
	max-width: 100%;
	height: auto;
	vertical-align: middle;
  }


header, footer{
	background-color: #AA0601;
}
.container{
	max-width: 1320px;
	padding-right: 12px;
	padding-left: 12px;
	margin: 0 auto;
}
.header{
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding-top: 16px;
	padding-bottom: 16px;
}
.logo a{
	display:block;
	width:80px;
	height:48px;
	background-image: url(https://github.com/hexschool/2022-web-layout-training/blob/main/week3-4/logo-white.png?raw=true);
	background-size: contain;
    background-repeat: no-repeat;
	text-indent: 101%;
    white-space: nowrap;
    overflow: hidden;
}
.menu{
	display: flex;
	flex-wrap: wrap;
	a{
		font-size: 20px;
		line-height: 30px;
		display:block;
		text-align:center;
		color:white;
		text-decoration: none;
		
	}
	li{
		padding-right: 48px;
	
	}
  }
.footer{
	padding-top: 60px;
	padding-bottom: 32px;
	color:white;
}
.list{
	display: flex;	
	a{
		color:white;
		text-decoration: none;

	}
}
.icon li img{
	
	width: 48px;
	height: 48px;

}
.footer-menu{
	display: flex;
	justify-content: space-between;
	align-items: flex-start;
}
.list li {

	margin-right: 48px;
	padding-bottom: 36px;

}
.img_3{

	display: flex;
	
}
.img_3 li{

	margin-right: 32px;

}
.footer-contact{

	font-size: 36px;
	margin-right: 22px;
	vertical-align: middle;
	line-height: 1.5;
}

.footer-contact ul{

margin-bottom: 32px;

}


.footer-copyright{
	display: flex;
	justify-content: space-between;
	border-top: 1px solid #fff;
	padding-top: 32px;
	
}
.footer-copyright ul{
	display: flex;
}
.footer-copyright ul{	
	a{color:white;
	  text-decoration: none;
	  margin-right: 32px;	
}
}
@media(max-width: 767px){
	.header{
	display: flex;
	flex-direction: column;
	align-items: center;
	flex-wrap: wrap;	
}
	header .container {
	padding: 0;
  }
}
@media(max-width: 767px) {
	.logo{
		padding-top: 12px;
		padding-bottom: 12px;
	}
}
@media(max-width: 767px) {
	.menu {
		flex-wrap: wrap;
		width: 100%;
	}
	.menu li{
		width: 50%;
		border: 1px solid #fff;
		margin:0;
	}	
	.menu li a{
	font-size: 16px;
	line-height: 24px;
	padding-right: 0;
	padding-top: 12px;
	padding-bottom: 12px;
		}
}
@media(max-width: 767px) {
	.footer{
		padding-top: 24px;
		padding-bottom: 24px;
	}
	.icon li img{
		width: 24px;
		height: 24px;
	}
	.list{
		display:none;
	}
	.icon li{
		margin-right: 8px;
	}
}
@media (max-width: 767px) {
	.footer-contact ul{
		font-size: 16px;
		line-height: 24px;
	}
	
}
@media (max-width: 767px) {
	.footer-copyright{
		display: flex;
		flex-direction: column;
	}
	.footer-copyright ul{
		display: block;
	}
	.footer-copyright p,li{
		margin-bottom: 8px;
	}

}
@media (max-width: 992px) {
	.footer{
		padding-top: 48px;
		padding-bottom: 40px;
	}
}
@media (max-width: 992px) {
	.icon li img{
		width: 40px;
		height: 40px;
	}
	.icon li{
		margin-right: 16px;
	}
}